.btn-nav::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 5px;  /* 100% will cover entire background of text */
  border-radius: 4px;
  background-color: var(--pec-blue-dark);
  bottom: 0;
  left: 0;  
}

.btn-nav {
  position: relative;
  font-size: 1.3rem;
  white-space: nowrap;
}
.btn-nav::before {
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}
.btn-nav:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}