.cursos{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    &__title--back {
        position: absolute;
        text-align: center;
        width: 100%;
        left: 0;
        top: 5%;
        font-weight: 900;
        font-size: 12rem;
        z-index: 1;
        opacity: .2;
    }
    &__curso{
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        transition: all 0.3s;
        position: relative;
        min-height: 51rem;
        &:hover{ 
           /*  .cursos__curso--overlay{
                opacity: 0;
            }
            .cursos__curso--link{
                background-color: #025173cb;
            } */
            .cursos__curso--title{
                color: white;
            }
            .cursos__curso--content{
                color: white;
            }
        }
        &--overlay{
            width: 100%;
            height: 100%;
            opacity: 0;
            background-color: var(--pec-gray-light);
            position: absolute;
            left: 0;
            top: 0;
            transition: all 0.3s;
            border-radius: 1rem;
        }
        &--link{
            background-color: transparent;
            transition: all 0.3s;
        }
        &--content{
            min-height: 9rem;
            overflow: hidden;
        }
        &--content-wrapper{
            bottom: 0;
            position: absolute;
            width: 100%;
            background: var(--pec-gray-dark);
            left: 0;
            right: 0;
            padding: 1rem 3rem;
            height: 60%;
            border-radius: 1rem;
        }
        &--img{
            position: absolute;
            width: 100%;
            height: auto;
            left: 0;
            top: 0;
            right: 0;
            margin: auto;
            border-radius: 1rem 1rem 0 0;
        }
        &--btn{
            width: 100%;
            position: absolute;
            bottom: 0;
        }
    }
}
@media (max-width: 500px){
    .cursos{
        &__title--back {
            font-size: 6rem;
            top: 2%;
        }
    }
}
.cursos-page{
    background-color: #08061c;
  }