.single-bg {
  background: #0a1812;
}
#pagina-grid-servicos > .container-fluid > .row {
  overflow: visible;
}
.post-card {
  min-height: 33rem;
  height: 60vh;
  transition: all 0.9s;
}
div.post-card {
  margin-bottom: 3%;
}
a.post-card {
  background-color: white;
}
/* div.post-card:not(:nth-child(4)) {
      margin-right: 1%;
    } */
a.post-card {
  padding-bottom: 10%;
  position: relative;
  overflow: hidden;
}
.servicos-titulo {
  color: var(--primaryColor);
  text-align: center;
  font-family: "Rajdhani", sans-serif !important;
  text-transform: uppercase;
  font-weight: 900 !important;
  font-size: 1.9rem;
  line-height: 1.9rem;
  padding: 0 13%;
}
.servicos-texto {
  font-family: Verdana;
  color: #6b6b6b;
  /* font-size: 0.7vw;
  display: flex;
  align-items: center;
  padding: 0 15% 0 15%; */
  /* text-align: center; */
}
.image-title-wrapper {
  width: 100%;
}
.servicos-imagem {
  width: auto !important;
  /* height: 40vh!important; */
  /* max-width: none!important; */
  margin-bottom: 10px;
  /* max-height: 30vh; */
  min-height: auto;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100%;
}
.wraper-card {
  height: 100%;
}
a:hover {
  text-decoration: none;
}
a.post-card:hover {
  transform: translateY(-15px) scale(1.1);
}
.vc_grid-item.vc_clearfix.vc_col-sm-3.vc_grid_filter-item.vc_visible-item:hover {
  transform: translateY(-15px) scale(1.1);
}
div.post-card:hover:nth-child(1) > a.post-card {
  transform: translateY(-15px) scale(1.1) translateX(15px);
}
div.post-card:hover:nth-child(4n + 1) > a.post-card {
  transform: translateY(-15px) scale(1.1) translateX(15px);
}
div.post-card:hover:nth-child(4) > a.post-card {
  transform: translateY(-15px) scale(1.1) translateX(-15px);
}
div.post-card:hover:nth-child(4n + 0) > a.post-card {
  transform: translateY(-15px) scale(1.1) translateX(-15px);
}
a.post-card {
  -webkit-box-shadow: 0px 14px 20px 0px rgba(0, 0, 0, 0.85);
  -moz-box-shadow: 0px 14px 20px 0px rgba(0, 0, 0, 0.85);
  box-shadow: 0px 14px 20px 0px rgba(0, 0, 0, 0.85);
}
a.post-card:hover {
  -webkit-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  -moz-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
}
div.post-card > a.post-card::before {
  content: "";
  display: block;
  height: 90%;
  width: 84%;
  position: absolute;
  top: 5%;
  left: 8%;
  z-index: 998;
  transition: all 0.4s;
  border: solid var(--primaryColor) 2px;
}
.paginate-links-wraper {
  color: var(--primaryColor);
  text-align: center;
  font-family: "Rajdhani", sans-serif !important;
  text-transform: uppercase;
  font-weight: 900 !important;
  font-size: 1.8rem;
  line-height: 1.6rem;
}

.paginate-links-wraper > .prev {
  font-size: 1.4rem;
}

.paginate-links-wraper > *:not(:last-child) {
  margin-right: 1%;
}
@media (max-width: 450px) {
  div.post-card:hover:nth-child(4n + 1) > a.post-card {
    transform: translateY(-15px) scale(1.1) translateX(0px);
  }
  div.post-card:hover:nth-child(1) > a.post-card {
    transform: translateY(-15px) scale(1.1) translateX(0px);
  }
  div.post-card:hover:nth-child(4n + 1) > a.post-card {
    transform: translateY(-15px) scale(1.1) translateX(0px);
  }
  div.post-card:hover:nth-child(4) > a.post-card {
    transform: translateY(-15px) scale(1.1) translateX(0px);
  }
  div.post-card:hover:nth-child(4n + 0) > a.post-card {
    transform: translateY(-15px) scale(1.1) translateX(0px);
  }
  .servicos-titulo {
    font-size: 2rem;
    line-height: 2rem;
  }
  .servicos-texto {
    font-size: 2.8vw;
  }
  .post-card {
    min-height: 107vw;
    height: 108vw;
  }
  div.post-card {
    margin-bottom: 13%;
  }
}

