body:not(.home) .wysiwyg,
.editor-styles-wrapper {
  @apply font-poppins;

  & .secondary-content-block ul {
    padding-left: 1.3rem;
    & li {
      list-style: disc;
      padding-left: 0.9rem;
      margin-left: 0.9rem;
    }
  }
  p {
    margin: 1em 0;
  }

  > h1,
  > h2,
  > h3,
  > h4,
  > h5,
  > h6,
  .wp-block-group h1,
  .wp-block-group h2,
  .wp-block-group h3,
  .wp-block-group h4,
  .wp-block-group h5,
  .wp-block-group h6 {
    @apply font-poppins font-bold;
    margin: 1.89em auto 1em;
  }

  > h1,
  > h2,
  > h3,
  > h4,
  > h5,
  > h6 {
    &:first-child {
      @apply mt-0;
    }
  }

  > h1,
  > h2,
  .wp-block-group h2 {
    @apply text-5xl;
  }

  > h3,
  .wp-block-group h3 {
    @apply text-2xl;
  }

  > h4,
  .wp-block-group h4 {
    @apply text-lg;
  }

  > h5,
  .wp-block-group h5 {
    @apply text-sm;
  }

  > h6,
  .wp-block-group h6 {
    @apply text-xs;
  }

  > ol,
  > ul,
  .wp-block-group ol,
  .wp-block-group ul {
    @apply list-outside relative;
    margin: 1em 0;

    li {
      @apply relative;
      padding-left: 2.25em;
      margin-bottom: 1em;

      &::before {
        @apply block absolute;
      }
    }
  }

  > ol,
  .wp-block-group ol {
    counter-reset: section;

    li::before {
      @apply font-bold top-0;
      counter-increment: section;
      content: counter(section) ".";
      left: 0.55em;
      right: calc(100% - 1.5em);
    }
  }

  > ul,
  .wp-block-group ul {
    li::before {
      @apply rounded-full;
      content: "";
      height: 0.25em;
      left: 1em;
      top: 0.65em;
      width: 0.25em;
    }
  }

  a:not(.btn) {
    //@apply text-main-1;
    transition: color 0.4s ease;

    &:hover {
      //@apply text-contrast;
    }
  }

  iframe {
    @apply block mx-auto;
    margin-bottom: 2.5em;
    margin-top: 2.5em;
  }

  .wp-block-image {
    @apply m-0;
  }

  .wp-block-image.size-large,
  .size-large img {
    @apply w-full;
  }

  #site & .blocks-gallery-item {
    @apply mb-0;
  }

  .wp-block-gallery {
    @apply border-b-2 border-gray-50 my-4 mx-auto;
  }

  figure {
    @apply flex-col;
  }

  figcaption,
  .wp-block-gallery figure figcaption {
    @apply text-xl font-bold font-poppins m-0 py-4 relative bg-none text-left flex-grow-0 overflow-hidden;
  }

  .wp-block-gallery figure figcaption {
    @apply border-none;
    text-overflow: ellipsis;
  }

  .wp-block-quote {
    @apply font-bold font-poppins relative border-t-2 border-b-2 border-gray-50 pt-16 pb-4 mx-0;

    &::before {
      @apply absolute left-1 top-0 text-7xl;
      content: "\201C";
    }

    @screen sm {
      @apply pt-10 pl-24;

      &::before {
        @apply left-0 top-auto;
      }
    }

    @screen md {
      @apply text-2xl;
    }
  }
}

.wp-block-column {

  p {
    margin: 1em 0;
  }
}
